.App {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.logout {
  text-decoration: none;
  width: 10vw;
  background-color: rgba(250, 250, 250, 0.5);
  backdrop-filter: blur(5px);
  color: black;
  padding: 30px 30px;
  font-size: 1.2em;
  transition-timing-function: ease-out;
    transition: 0.25s;
    margin-top: 50px;
}
.logout:hover {
  border-radius: 30px;
  transition-timing-function: ease-in;
}
.main {
  display: grid;
  justify-items: center;
  
}