body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:  #77DD77
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.logout {
  text-decoration: none;
  width: 10vw;
  background-color: rgba(250, 250, 250, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  color: black;
  padding: 30px 30px;
  font-size: 1.2em;
  transition-timing-function: ease-out;
    transition: 0.25s;
    margin-top: 50px;
}
.logout:hover {
  border-radius: 30px;
  transition-timing-function: ease-in;
}
.main {
  display: grid;
  justify-items: center;
  
}
.navigation {
    width: 100%;
    display: grid;
    justify-content: center;
}
li a {
    width: 6em;
    display: block;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 1.4em;
    border-radius: 10px;
    transition-timing-function: ease-out;
    transition: 0.5s;
    color:black;

  }
li a:hover {
  background-color: rgba(150, 150, 150, 0.5);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  color: white;
  transition-timing-function: ease-in;

}
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
.img {
    width: 40%;
    background:none;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 0px;
}
.username {
    font-size: 2em;
}
.user {
    display: grid;
    justify-items: center;
    width: 100%;
}
.login {
    text-decoration: none;
    color: black;
    padding: 3em;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.login:hover {
    font-size: 3em;
}
.outside {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    justify-items: center;
}
.card {
    width: 15vw;
    height: 50vh;
    display: grid;
    justify-content: center;
    margin-top: 1em;

}
.cardinside {
    width: 80%;
    display: grid;
    justify-items: center;
    margin: auto;
    padding: 1em;
}
h1 {
    font-size: 1em;
}
img {
    width: 70%;
    margin: auto;

    border-radius: 50%;
}
.titles {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 50%;
}
.titles h1 {
    text-align: center;
  display: flex; /* added this line*/
  align-items: center;
  font-size: 1.3em;
  background-color: white;
}
.titles h3 {
    text-align: center;
  display: flex; /* added this line*/
  align-items: center;
}
